// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import {
  BTN_TEXT,
  EQUIPMENT_MANAGEMENT_REPORT,
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
import spotCheckStatisticsRz from '@/views/report/EquipmentManagement/spotCheckStatisticsRz.vue';
import spotCheckStatistics from '@/views/report/EquipmentManagement/spotCheckStatistics.vue';
import maintenanceStatistics from '@/views/report/EquipmentManagement/maintenanceStatistics.vue';
import repairStatistics from '@/views/report/EquipmentManagement/repairStatistics.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import toolLifeStatistics from '@/views/report/EquipmentManagement/toolLifeStatistics.vue';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import {
  GetCheckStatisticalQueryParameter,
  GetListQueryParameterMaintenance,
  GetListQueryParameterRepair,
  GetListQueryParameterTool
} from '@/api/equipmentmanagement';
import { REPORT_TOOL, ISANDON, SYSTEM_CONFIG } from '@/utils/ajaxConfigure';
export default {
  name: 'EquipmentManagement',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      CHECK_IMG: false,
      active: 0,
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      dateTime: '',
      isAndon: ISANDON,
      // 默认引入的组件名称
      currentView: '',
      tabs: [
        {
          name: EQUIPMENT_MANAGEMENT_REPORT.SPOT_CHECK_STATISTICS,
          views: ''
        },
        {
          name: EQUIPMENT_MANAGEMENT_REPORT.REPAIR_STATISTICS,
          views: 'repairStatistics'
        },
        {
          name: EQUIPMENT_MANAGEMENT_REPORT.MAINTENANCE_STATISTICS,
          views: 'maintenanceStatistics'
        }
      ],
      tabs_andon: [
        {
          name: EQUIPMENT_MANAGEMENT_REPORT.SPOT_CHECK_STATISTICS,
          views: 'spotCheckStatistics'
        },
        {
          name: EQUIPMENT_MANAGEMENT_REPORT.REPAIR_STATISTICS,
          views: 'repairStatistics'
        }
      ],
      pickerOptions: {
        /*
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ],
       */
        disabledDate: (current) => {
          return current.getTime() > this.moment(`${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`).format('x');
        }

      }
    };
  },
  created() {
    var that = this;
    if (REPORT_TOOL) {
      this.tab.push({
        name: EQUIPMENT_MANAGEMENT_REPORT.TOOL_LIFE_STATISTICS,
        views: 'toolLifeStatistics'
      });
    }

    if (SYSTEM_CONFIG[localStorage.getItem('companyId')]) {
      this.CHECK_IMG = SYSTEM_CONFIG[localStorage.getItem('companyId')].CHECK_IMG;
    }

    this.$nextTick(() => {
      if (this.CHECK_IMG) {
        that.tabs[0].views = 'spotCheckStatisticsRz';
        that.currentView = 'spotCheckStatisticsRz';
      } else {
        that.tabs[0].views = 'spotCheckStatistics';
        that.currentView = 'spotCheckStatistics';
      }
    });

    // 调用列表查询接口
    this.getSpotCheckQueryParameter();

    if (this.getUrlKeyMain('tab', window.location.href)) {
      // alert(this.getUrlKeyMain('tab', window.location.href));
      var tab = this.getUrlKeyMain('tab', window.location.href);
      switch (tab) {
        case '1':
          this.active = 0;
          if (this.CHECK_IMG) {
            this.currentView = 'spotCheckStatisticsRz';
          } else {
            this.currentView = 'spotCheckStatistics';
          }
          break;
        case '2':
          this.active = 1;
          this.currentView = 'repairStatistics';
          break;
        case '3':
          this.active = 2;
          this.currentView = 'maintenanceStatistics';
          break;
        case '4':
          this.active = 3;
          this.currentView = 'toolLifeStatistics';
          break;
      }
    }
  },
  methods: {
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    // 按钮触发事件
    buttonClickEvent(type) {
      // console.log(type);
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      // console.log(i, v);
      this.active = i;
      this.currentView = v;
      // this.$refs.searchBox.resetCondtionGroup();
      if (this.active === 0) {
        this.getSpotCheckQueryParameter();
        // this.GetListQueryParameterMaintenance();
      } else if (this.active === 1) {
        this.isSearchBoxShow = false;
        // this.GetListQueryParameterRepair();
      } else if (this.active === 2) {
        this.isSearchBoxShow = false;
        // this.GetListQueryParameterMaintenance();
      } else {
        this.isSearchBoxShow = false;
        // this.GetListQueryParameterTool();
      }
    },
    complexReset() {
      if (this.active === 0) {
        this.dateTime = [];
      }
    },
    refresh() {
      if (this.active === 0) {
        this.$refs.spotCheckStatistics.findList();
      } else if (this.active === 1) {
        this.$refs.repairStatistics.findList();
      } else if (this.active === 2) {
        this.$refs.maintenanceStatistics.findList();
      } else {
        this.$refs.toolLifeStatistics.findList();
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        if (this.CHECK_IMG) {
          this.$refs.spotCheckStatisticsRz.exportTable();
        } else {
          this.$refs.spotCheckStatistics.exportTable();
        }
      } else if (this.active === 1) {
        this.$refs.repairStatistics.exportTable();
      } else if (this.active === 2) {
        this.$refs.maintenanceStatistics.exportTable();
      } else {
        this.$refs.toolLifeStatistics.exportTable();
      }
    },
    async getSpotCheckQueryParameter() {
      try {
        const res = await GetCheckStatisticalQueryParameter();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.searchList = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async GetListQueryParameterMaintenance() {
      try {
        const res = await GetListQueryParameterMaintenance({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async GetListQueryParameterRepair() {
      try {
        const res = await GetListQueryParameterRepair({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async GetListQueryParameterTool() {
      try {
        const res = await GetListQueryParameterTool({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      if (this.active === 0) {
        if (this.CHECK_IMG) {
          this.$refs.spotCheckStatisticsRz.complexSearch(
            conditionArr1,
            groupOperator,
            conditionArr2,
            this.dateTime
          );
        } else {
          this.$refs.spotCheckStatistics.complexSearch(
            conditionArr1,
            groupOperator,
            conditionArr2,
            this.dateTime
          );
        }
      } else if (this.active === 1) {
        this.$refs.repairStatistics.findTable(
          conditionArr1,
          groupOperator,
          conditionArr2
        );
      } else if (this.active === 2) {
        this.$refs.maintenanceStatistics.findTable(
          conditionArr1,
          groupOperator,
          conditionArr2
        );
      } else {
        this.$refs.toolLifeStatistics.findTable(
          conditionArr1,
          groupOperator,
          conditionArr2
        );
      }
    }
  },
  components: {
    // 加载未审核组件
    spotCheckStatisticsRz,
    HeaderTitle,
    SearchBox,
    HeadOperationItem,
    spotCheckStatistics,
    maintenanceStatistics,
    repairStatistics,
    toolLifeStatistics
  }
};
